.LoginPopup {
  position: absolute;
  height: 50px;
  top: 55px;
  background: white;
  z-index: 99999;
  border-radius: 5px;
  -webkit-box-shadow: 0px 3px 10px hsla(0, 0%, 0%, 0.4);
          box-shadow: 0px 3px 10px hsla(0, 0%, 0%, 0.4);
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
}


